.SemaphoreCheckbox {
  font-size: 20px;
}

.SemaphoreCheckbox label {
  font-weight: normal;
  margin-left: 5px;
}

.SemaphoreCharacter {
  position: relative;
}

.SemaphoreCharacter svg {
  margin-right: 3px;
}

.SemaphoreCharacter .SemaphoreCheckbox {
  position: absolute;
}

.SemaphoreCharacter .SemaphoreCheckbox.SemaphoreCheckbox-north {
  top: 16px;
  left: 112.5px;
}

.SemaphoreCharacter .SemaphoreCheckbox.SemaphoreCheckbox-northEast {
  top: 47px;
  left: 192px;
}

.SemaphoreCharacter .SemaphoreCheckbox.SemaphoreCheckbox-east {
  top: 103px;
  left: 203px;
}

.SemaphoreCharacter .SemaphoreCheckbox.SemaphoreCheckbox-southEast {
  top: 149px;
  left: 169px;
}

.SemaphoreCharacter .SemaphoreCheckbox.SemaphoreCheckbox-south {
  top: 161px;
  left: 112.5px;
}

.SemaphoreCharacter .SemaphoreCheckbox.SemaphoreCheckbox-southWest {
  top: 149px;
  left: 57px;
}

.SemaphoreCharacter .SemaphoreCheckbox.SemaphoreCheckbox-west {
  top: 103px;
  left: 25px;
}

.SemaphoreCharacter .SemaphoreCheckbox.SemaphoreCheckbox-northWest {
  top: 47px;
  left: 35px;
}

.SemaphoreStream {
  margin: 10px 15px 0px;
}

.SemaphoreStream-input {
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
}

.SemaphoreStream-view {
  margin-left: 20px;
  font-size: 72px;
  font-weight: bold;
}

.SemaphoreStream-commands {
  margin-bottom: 10px;
}

.SemaphoreStream-output {
  font-size: 24px;
  min-height: 56px;
  white-space: pre-wrap;
}

.SemaphoreTable .table-responsive {
  border-top: none;
}
  
.SemaphoreTable th,
.SemaphoreTable td {
  text-align: center;
}

.Semaphore-content {
  padding: 0px 0px 20px;
}

